.container .search {
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 1em; /* cambios 0  | -1.5em*/
  width: 80px;
  height: 80px;
  background: transparent;
  border-radius: 50%;
  transition: all 1s;
  z-index: 4;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.4);
  line-height: 1px;
  /* border: 1px solid red; */
  display: flex;
  justify-content:center;
  align-items: center;
  
}

