#searchInput:left {
  /* border: 1px solid red; */
  /* left: -9em; */
  /* top: 110px; */
  /* position: relative; */
  display: absolute;
  left: 3em;
}


.container input:hover {
  cursor: pointer;
}
.container input:focus {
  width: 300px;
  opacity: 1;
  cursor: text;
  /* border: 1px solid red; */
}
.container input:focus ~ .search {
  right: -250px;
  background: #151515;
  z-index: 6;
  line-height: 1px;
}
.container input:focus ~ .search::before {
  top: 0;
  left: 0;
  width: 25px;
}
.container input:focus ~ .search::after {
  top: 0;
  left: 0;
  width: 25px;
  height: 2px;
  border: none;
  background: white;
  border-radius: 0%;
  transform: rotate(-45deg);
}
.container input::placeholder {
  color: white;
  opacity: 0.5;
  font-weight: bolder;
  /* border: 1px solid red; */
}