body {
    background:linear-gradient( 
        #BAA182 0%, #e2d9b8 50%, #EED37A 100%);
        /* #f0e9cd 0%, #e2d9b8 50%, #f0e9cd 100%); */
    /* width: 350px; */
     height: 106vh; 
    display: grid;
    justify-content:center;
    align-items: center;  
    /* border: 4px solid var(--border5);  */
    background-repeat: repeat-x;
    line-height:1.5px ;
}


@media (max-width:320px) {
    .body {
        background:linear-gradient( 
        #BAA182 0%, #e2d9b8 50%, #EED37A 100%);
        /* width: 350px; */
     height: 106vh; 
    display: flex;
    justify-content:center;
    align-items: center;  
    background-repeat: repeat-x;
    }
}


@media screen and (max-width: 300px) {
    body {
        height:118vh;
    }
}


.card {
    /* border: 2px solid var(--border5); */
    /* display: grid;
    place-items: center; */
    /* grid-template-columns: repeat(3, 1fr); */
