.buttonx {
     border:none;
    background-color: transparent;
    /* 
    position: relative;
     */
     box-shadow: 0 0 25px 0 #C2935F, 0 20px 25px 0 rgba(0, 0, 0, 0.2);
    position: relative;
    z-index: 1;
    bottom: -140px;
    background: #C2935F;
    border-radius: 50%;
    border: none;
    transition: box-shadow 400ms cubic-bezier(0.2, 0, 0.7, 1), transform 200ms cubic-bezier(0.2, 0, 0.7, 1);
    
}

.buttonx:hover {
    transform: rotate(45deg);
    box-shadow:  0 0 2px 10px rgba(138, 59, 88, 0.1), 0 0 3px 15px rgba(138, 59, 88, 0.1);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
}

.buttonx:after {
    content: "+";
    font-size: 2.5em;
    line-height: 1.1em;
  }

.buttonx:after {
    content: "";
    position: absolute;
    z-index: 0;
  }


